.search {
  position: sticky;
  top: 0;
  padding: 1rem;
  background-color: #fff;
  /* border: 1px solid black; */
}

.search > input {
  display: block;
  width: 100%;
  padding: 10px 1rem;
  margin: 1rem auto 10px auto;
  outline: none;
  border: none;
  border-radius: 1rem;
  background-color: #eee;
  font-size: 1.2rem;
}

.search__button {
  display: flex;
  justify-content: flex-end;
}

.search__button > button {
  padding: 5px 1rem;
  outline: none;
  border: none;
  border-radius: 1rem;
  color: white;
  background-color: black;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
}
