@import url(https://fonts.googleapis.com/css2?family=Newsreader:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Newsreader", serif;
}

a {
  text-decoration: none;
}

.main__header {
  width: 100%;
  box-shadow: 0 0 5px #ccc;
  background-color: #fff;
}

.main__navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1000px;
  height: 100%;
  margin: auto;
  padding: 1rem;
  /* border: 1px solid springgreen; */
}

.main_navbar__logo {
  font-size: 2.5rem;
  font-weight: 600;
  font-style: italic;
}

.card {
  display: flex;
  max-width: 600px;
  width: 100%;
  margin-bottom: 2rem;
  padding: 1rem;
  box-shadow: 0 0 10px #ccc;
  border-radius: 1rem;
  /* border: 1px solid steelblue; */
}

.card__left {
  height: 100%;
  /* border: 1px solid tomato; */
}

.card__image {
  width: 100px;
  height: 100px;
}

.card__image > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.card__right {
  width: 450px;
  height: 100%;
  padding: 0 1rem;
  /* border: 1px solid darkred; */
}

.card__right > a {
  color: black;
}

.card__actions {
  display: flex;
  justify-content: flex-end;
  /* border: 1px solid darkviolet; */
}

.card__actions > a {
  font-size: 1.5rem;
  color: #1da1f2;
  cursor: pointer;
}

@media screen and (max-width: 450px) {
  .card {
    flex-wrap: wrap;
  }
  .card__image {
    width: 100%;
    height: 100%;
  }
  .card__image > img {
    border-radius: 5px;
  }
}

.card__loading {
  display: flex;
  width: 100%;
  margin-bottom: 2rem;
  padding: 1rem;
  box-shadow: 0 0 10px #ccc;
  border-radius: 1rem;
  /* border: 1px solid steelblue; */
}

.card_loading__left {
  height: 100%;
  /* border: 1px solid tomato; */
}

.card_loading__image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #eee;
}

.card_loading__right {
  max-width: 450px;
  width: 100%;
  height: 100%;
  padding: 0 1rem;
  /* border: 1px solid darkred; */
}

.card_loading__title {
  width: 100%;
  height: 20px;
  margin-bottom: 10px;
  border-radius: 1rem;
  background-color: #eee;
}

.card_loading__paragraph {
  width: 80%;
  height: 10px;
  margin-bottom: 10px;
  border-radius: 1rem;
  background-color: #eee;
}

.card_loading__half__paragraph {
  width: 50%;
  height: 10px;
  border-radius: 1rem;
  background-color: #eee;
}

.search {
  position: sticky;
  top: 0;
  padding: 1rem;
  background-color: #fff;
  /* border: 1px solid black; */
}

.search > input {
  display: block;
  width: 100%;
  padding: 10px 1rem;
  margin: 1rem auto 10px auto;
  outline: none;
  border: none;
  border-radius: 1rem;
  background-color: #eee;
  font-size: 1.2rem;
}

.search__button {
  display: flex;
  justify-content: flex-end;
}

.search__button > button {
  padding: 5px 1rem;
  outline: none;
  border: none;
  border-radius: 1rem;
  color: white;
  background-color: black;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
}

.home__page {
  display: flex;
  justify-content: center;
}

.feed {
  width: 100%;
  max-width: 600px;
  padding: 1rem;
  /* border: 1px solid springgreen; */
}

